@import './../../../assets/style/vars.scss';

.container {
    margin: 0 auto;
    margin-top: 41px;
    max-width: 364px;
    padding-top: 24px;
    padding-bottom: 24px;
    background-color: white;
}

.footer {
    text-align: center;

    & > h1 {
        font-size: 18px;
    }

    & > p {
        font-size: 14px;
        color: $text-color-grey;
    }
}

.df {
    display: flex;
    margin-top: 14px;
    font-size: 12px;
    font-weight: 700;
    margin-bottom: 24px;
}

.reg {
    width: 50%;
    text-align: center;
    padding-bottom: 7px;
    border-bottom: 2px solid $active-color-blue;


    & > p {
        color: $active-color-blue;
    }
}

.auth {
    width: 50%;
    text-align: center;
    padding-bottom: 8px;
    border-bottom: 1px solid $text-color-grey;
}

.auth p {
    color: black;
}

.checkbox {
    margin-left: 24px;
    margin-bottom: 24px;
}

.checkbox__input {
    width: 16px;
    height: 16px;
    position: relative;
    top: 8px;
}

.checkbox__text {
    max-width: 168px;
    margin-left: 13px;
    font-size: 12px;
    padding-bottom: 10px;
}

.agreement_text {
    color: $active-color-blue;
    font-size: 12px;
    margin-left: 29px;
}

.agreement_text:hover {
    border-bottom: 1px solid $active-color-blue;
}

.button {
    display: block;
    outline: none;
    cursor: pointer;
    border: none;
    background-color: $active-color-blue;
    color: white;
    border-radius: 5px;
    font-size: 16px;
    line-height: 24px;
    font-weight: 600;
    width: calc(100% - (364px - 316px));
    height: 48px;
    margin-left: 24px;
  }

  .error {
    font-size: 12px;
    margin-left: 24px;
    margin-bottom: 12px;
    margin-top: -12px;
    color: red;
  }

  .form svg {
    position: relative;
    bottom: 32px;
    left: 84%;
    cursor: pointer;
  }

 .form input {
    margin-bottom: 0;
 }

 .nav_list {
    display: none;
 }

 @media only screen and (max-device-width: 1200px){
    .nav_list {
        display: block;
        position: absolute;
        top: 72px;
        left: calc(100% - (1200px - 978px));
        background-color: $background;
        padding: 14px;
        padding-right: 24px;
        font-size: 24px;
      }
 }

 @media only screen and (max-device-width: 768px){
    .nav_list {
        left: 0;
      }
 }